@media all and (orientation: portrait) {
  .container {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #dedace;
    border-bottom-right-radius: 80px;
    box-shadow: 0 0 30px #292826;
  }

  .display {
    position: absolute;
    left: 10px;
    right: 10px;
    top: 5vh;
    height: calc((100vw - 30px) / 2);
    border-radius: 10px;
    background: #000;
    padding: 10px;
  }

  .loading {
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
  }

  .wolf {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -20px;
    text-align: center;
    font-size: 12px;
    color: #666;
    font-weight: bold;
  }

  .cross {
    position: absolute;
    background-image: url("image/buttons/cross.png");
    background-repeat: no-repeat;
    background-size: 100%;
    left: 10px;
    top: calc((100vw - 30px) / 2 + 30vh);
    width: 48vw;
    height: 48vw;
  }

  .crossLeft {
    position: absolute;
    width: 14vw;
    height: 14vw;
    top: 13vw;
  }

  .crossRight {
    position: absolute;
    width: 14vw;
    height: 14vw;
    top: 13vw;
    right: 0;
  }

  .crossUp {
    position: absolute;
    width: 14vw;
    height: 13vw;
    left: 13vw;
  }

  .crossDown {
    position: absolute;
    width: 14vw;
    height: 13vw;
    left: 13vw;
    bottom: 0;
  }

  .canvas {
    width: 100%;
    height: 100%;
  }

  .leftButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 26vw;
    height: 13vw;
    top: calc((100vw - 30px) / 2 + 17vh);
    left: 0;
    background-image: url("image/buttons/l.png");
  }

  .leftButton:active {
    background-image: url("image/buttons/l_pressed.png");
  }

  .rightButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 26vw;
    height: 13vw;
    top: calc((100vw - 30px) / 2 + 17vh);
    right: 0;
    background-image: url("image/buttons/r.png");
  }

  .rightButton:active {
    background-image: url("image/buttons/r_pressed.png");
  }

  .aButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 22vw;
    height: 22vw;
    top: calc((100vw - 30px) / 2 + 32vh + 22vw);
    right: calc(5px + 18vw);
    background-image: url("image/buttons/a.png");
  }

  .aButton:active {
    background-image: url("image/buttons/a_pressed.png");
  }

  .bButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 22vw;
    height: 22vw;
    top: calc((100vw - 30px) / 2 + 32vh);
    right: 5px;
    background-image: url("image/buttons/b.png");
  }

  .bButton:active {
    background-image: url("image/buttons/b_pressed.png");
  }

  .tools {
    position: absolute;
    bottom: 15px;
    left: 0;
    right: 0;
    text-align: center;
  }

  .toolButton {
    background-color: rgb(141, 77, 115);
    width: 64px;
    height: 24px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    border-radius: 4px;
    color: white;
    font-weight: bold;
    box-shadow: 0px 0px 6px rgb(111, 111, 111);
    cursor: pointer;
  }
}

@media all and (orientation: landscape) {
  .container {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #dedace;
    border-bottom-right-radius: 100px;
    box-shadow: 0 0 30px #292826;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .display {
    flex: 1;
    margin: 0 10px;
    border-radius: 10px;
    background: #000;
    padding: 10px 10px 8px 10px;
    position: relative;
  }

  .loading {
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
  }

  .wolf {
    position: absolute;
    left: 0;
    right: 0;
    top: -25px;
    text-align: center;
    font-size: 14px;
    color: #666;
    font-weight: bold;
  }

  .cross {
    background-image: url("image/buttons/cross.png");
    background-repeat: no-repeat;
    background-size: 100%;
    bottom: 10px;
    width: 20vw;
    height: 20vw;
    transform: translateX(5px);
  }

  .crossLeft {
    position: absolute;
    width: 6.8vw;
    height: 6.8vw;
    top: 6.6vw;
  }

  .crossRight {
    position: absolute;
    width: 6.8vw;
    height: 6.8vw;
    top: 6.6vw;
    right: 0;
  }

  .crossUp {
    position: absolute;
    width: 6.8vw;
    height: 6.6vw;
    left: 6.6vw;
  }

  .crossDown {
    position: absolute;
    width: 6.8vw;
    height: 6.8vw;
    left: 6.6vw;
    bottom: 0;
  }

  .canvas {
    width: 100%;
    height: 100%;
  }

  .leftButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 18vw;
    height: 9vw;
    top: 5vh;
    left: 0;
    background-image: url("image/buttons/l.png");
  }

  .leftButton:active {
    background-image: url("image/buttons/l_pressed.png");
  }

  .rightButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 18vw;
    height: 9vw;
    top: 5vh;
    right: 0;
    background-image: url("image/buttons/r.png");
  }

  .rightButton:active {
    background-image: url("image/buttons/r_pressed.png");
  }

  .actionButtons {
    width: 20vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .aButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 10vw;
    height: 10vw;
    right: 10vw;
    transform: translateY(5vw) translateX(-5px);
    background-image: url("image/buttons/a.png");
  }

  .aButton:active {
    background-image: url("image/buttons/a_pressed.png");
  }

  .bButton {
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 10vw;
    height: 10vw;
    right: 0;
    transform: translateY(-2vw) translateX(-5px);
    background-image: url("image/buttons/b.png");
  }

  .bButton:active {
    background-image: url("image/buttons/b_pressed.png");
  }

  .tools {
    position: absolute;
    bottom: 12px;
    left: 0;
    right: 0;
    text-align: center;
  }

  .toolButton {
    background-color: rgb(141, 77, 115);
    width: 64px;
    height: 24px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    border-radius: 4px;
    color: white;
    font-weight: bold;
    box-shadow: 0px 0px 6px rgb(111, 111, 111);
    cursor: pointer;
  }
}
