@media all and (orientation: portrait) {
  .settingIcon {
    position: fixed;
    left: 22px;
    bottom: 22px;
    background-repeat: no-repeat;
    background-size: 70%;
    width: 56px;
    height: 56px;
    background-image: url("../../image/setting.png");
    background-position: center center;
    border-radius: 50%;
    background-color: #3e3d41;
    border: 3px solid #000;
    box-shadow: 0 0 10px #000;
  }
}

@media all and (orientation: landscape) {
  .settingIcon {
    position: fixed;
    left: 10px;
    bottom: 10px;
    background-repeat: no-repeat;
    background-size: 70%;
    width: 56px;
    height: 56px;
    background-image: url("../../image/setting.png");
    background-position: center center;
    border-radius: 50%;
    background-color: #3e3d41;
    border: 4px solid #222;
    box-shadow: 4px 4px 4px rgba(1, 1, 1, 0.3);
  }
}

.settingIcon:active {
  opacity: 0.75;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(1, 1, 1, 0.5);
  justify-content: center;
  align-items: center;
  z-index: 99;
}

.container {
  width: 66vw;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation-name: modalConatiner;
  animation-duration: 0.3s;
  text-align: center;
}

@keyframes modalConatiner {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.button {
  padding: 12px 0;
  text-align: center;
  border-bottom: 1px solid #aaa;
  font-weight: bold;
}

.button:active {
  background-color: #eee;
}

.button2 {
  padding: 12px 0;
  text-align: center;
  font-weight: bold;
}

.button2:active {
  background-color: #eee;
}
