.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(1, 1, 1, 0.5);
  justify-content: center;
  align-items: center;
  z-index: 99;
}

.container {
  width: calc(100vw - 100px);
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation-name: modalConatiner;
  animation-duration: 0.3s;
}

@keyframes modalConatiner {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.message {
  padding: 15px 15px;
  font-weight: bold;
  line-height: 24px;
  text-align: center;
}

.okButton {
  border-top: 1px solid #aaa;
  padding: 12px 0;
  text-align: center;
}

.okButton:active {
  background-color: #eee;
}
