@media all and (orientation: portrait) {
  .game {
    display: inline-block;
    width: 20vw;
    height: calc(20vw + 20px);
    background-color: rgb(169, 170, 172);
    position: relative;
    border-radius: 6px;
    margin: 1vw 1vw;
    box-sizing: 0 0 4px rgba(1, 1, 1, 0.6);
  }
}

@media all and (orientation: landscape) {
  .game {
    display: inline-block;
    width: 80px;
    height: 100px;
    background-color: rgb(169, 170, 172);
    position: relative;
    border-radius: 6px;
    margin: 1vw 1vw;
    box-sizing: 0 0 4px rgba(1, 1, 1, 0.6);
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(1, 1, 1, 0.5);
  justify-content: center;
  align-items: center;
  z-index: 99;
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(1, 1, 1, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

.gameLoading {
  width: 100px;
  height: 100px;
  background-color: #000;
  color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
}

.container {
  width: calc(100vw - 20px);
  height: calc(100vh - 20px);
  box-sizing: border-box;
  background-color: #dedace;
  border-radius: 4px;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  animation-name: modalConatiner;
  animation-duration: 0.3s;
}

@keyframes modalConatiner {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.title {
  padding: 8px 0;
  font-weight: bold;
  color: #680b62;
  font-size: 16px;
  font-weight: bold;
}

.gameNum {
  font-weight: bold;
  color: #680b62;
  font-size: 12px;
  font-weight: bold;
}

.games {
  flex: 1;
  border-top: 1px solid #680b62;
  border-bottom: 1px solid #680b62;
  overflow-y: auto;
  padding: 2vw 0;
}

.romInput {
  position: absolute;
  top: -100px;
}

.icon {
  position: absolute;
  left: 10px;
  top: 10px;
  right: 10px;
  bottom: 40px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("../../image/gameboy.png");
}

.name {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 3px;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-weight: bold;
  padding: 0 5px;
  text-align: center;
}

.tools {
  height: 40px;
  padding-top: 12px;
  position: relative;
}

.buttonImport {
  text-align: center;
  background-color: rgb(84, 175, 137);
  font-size: 13px;
  padding: 6px 15px;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  box-shadow: 0px 0px 6px rgb(111, 111, 111);
  cursor: pointer;
  position: absolute;
  top: 12px;
}

.buttonCancel {
  text-align: center;
  background-color: #ccc;
  font-size: 13px;
  padding: 6px 15px;
  border-radius: 4px;
  color: black;
  font-weight: bold;
  box-shadow: 0px 0px 6px rgb(111, 111, 111);
  cursor: pointer;
  margin-right: 15px;
  position: absolute;
  right: 60px;
  top: 12px;
}

.buttonOk {
  text-align: center;
  background-color: rgb(141, 77, 115);
  font-size: 13px;
  padding: 6px 15px;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  box-shadow: 0px 0px 6px rgb(111, 111, 111);
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 12px;
}
